/**
 * Compute a WebSocket URL for a given API path.
 * When deployed together (backend serves frontend), uses same origin.
 * For local development, defaults to localhost:8083.
 */
export function getWebSocketUrl(path: string): string {
  const { protocol, hostname, port } = window.location;

  // For local development (Vite dev server on port 5173)
  if (port === '5173' || port === '3000') {
    const wsProto = protocol === 'https:' ? 'wss:' : 'ws:';
    const wsUrl = `${wsProto}//${hostname}:8083${path.startsWith('/') ? path : `/${path}`}`;
    console.log('Using local dev WebSocket URL:', wsUrl);
    return wsUrl;
  }

  // For production (backend serves frontend) - use same origin
  const wsProto = protocol === 'https:' ? 'wss:' : 'ws:';
  const wsUrl = `${wsProto}//${hostname}${port ? `:${port}` : ''}${path.startsWith('/') ? path : `/${path}`}`;
  console.log('Using same-origin WebSocket URL:', wsUrl);
  return wsUrl;
}